"use strict";

import CharacterCanvas from "../../lib/CharacterCanvas";

const mineSweeper = {
  run: function() {
    let width = 16, height = 16, minesAmount = 40;

    if (location.search) {
      const params = location.search.slice(1).split("&");

      for (const param of params) {
        const splitted = param.split("=");
        
        switch (splitted[0]) {
          case "width":
            width = +splitted[1];
            break;
          case "height":
            height = +splitted[1];
            break;
          case "mines":
            minesAmount = +splitted[1];
            break;
        }
      }
    }

    const size = { width, height };
    const canvas = new CharacterCanvas("character-canvas", size.width, size.height, {
      fontFamily: "Lucida Console, Monaco, monospace"
    });

    let firstClick = true;
    let cells = [];
    let clickLoopHistory = [];
    let mines;
    let gameOver = false;
    let completed = false;

    for (let row of canvas.rows) {
      cells.push([]);
      for (let char of row.characters) {
        const coords = char.getCoordinates();
        cells[coords.y].push({
          open: false,
          mine: false,
          flag: false
        }); 

        char.addEventListener("click", () => {
          if (!gameOver && !completed) {
            click(coords);
          }
          else {
            canvas.clear();

            firstClick = true;
            gameOver = false;
            completed = false;

            for (let y = 0; y < cells.length; y++) {
              for (let x = 0; x < cells[y].length; x++) {
                cells[y][x] = {
                  open: false,
                  mine: false,
                  flag: false
                }
              }
            }
          }
        });

        char.addEventListener("contextmenu", (evt) => {
          evt.preventDefault();

          rightClick(coords);
        });
      }
    }

    const click = (coords, loop = false) => {
      const cell = cells[coords.y][coords.x];

      if (firstClick) {
        firstClick = false;
        mines = getMines(coords);

        for (let y = 0; y < cells.length; y++) {
          for (let x = 0; x < cells[y].length; x++) {
            cells[y][x].surroundingMines = countSurroundingMines(x, y);
          }
        }
      }
      else {
        if (cell.mine) {
          gameOver = true;
        }
      }

      const hasBeenClicked = clickLoopHistory.some(hCoords => hCoords.x === coords.x && hCoords.y === coords.y);

      if (cell.surroundingMines === 0 && !hasBeenClicked) {
        for (let x = -1; x < 2; x++) {
          for (let y = -1; y < 2; y++) {
            const xCoord = coords.x + x;
            const yCoord = coords.y + y;

            if (xCoord > -1 && xCoord < size.width && yCoord > -1 && yCoord < size.height) {
              clickLoopHistory.push(coords);
              click({x: xCoord, y: yCoord}, true);
            }
          }
        }
      }

      cell.open = true;

      if (!loop) {
        clickLoopHistory = [];
        completed = checkIfCompleted();
        draw();
      }
    }

    const rightClick = (coords) => {
      const cell = cells[coords.y][coords.x];

      if (!cell.open) {
        cell.flag = !cell.flag;
      }

      draw();
    }

    const draw = () => {
      canvas.clear();

      for (let y = 0; y < cells.length; y++) {
        for (let x = 0; x < cells[y].length; x++) {
          if (cells[y][x].open) {
            canvas.setCharacter(x, y, cells[y][x].surroundingMines.toString(), {
              color: "white"
            });
          }

          if (cells[y][x].flag && !cells[y][x].open) {
            canvas.setCharacter(x, y, "!", {
              color: "yellow"
            });
          }

          if ((gameOver || completed) && cells[y][x].mine) {
            canvas.setCharacter(x, y, "Â¤", {
              color: completed ? "green" : "red"
            });
          }
        }
      }
    }

    const getMines = (coords) => {
      let occupied = [];

      for (let x = -1; x < 2; x++) {
        for (let y = -1; y < 2; y++) {
          occupied.push({x: coords.x + x, y: coords.y + y});
        }
      }

      do {
        let x = Math.floor(Math.random() * size.width);
        let y = Math.floor(Math.random() * size.height);

        if (!occupied.some(c => c.x === x && c.y === y)) {
          occupied.push({x, y});
        }

      } while (occupied.length < minesAmount + 9);

      let mines = occupied.slice(9, occupied.length);

      mines.forEach(mine => {
        cells[mine.y][mine.x].mine = true;
      });

      return mines;
    }

    const countSurroundingMines = (cellX ,cellY) => {
      let count = 0;

      for (let x = -1; x < 2; x++) {
        for (let y = -1; y < 2; y++) {
          if (!(x === 0 && y === 0)) {
            const xCoord = cellX + x;
            const yCoord = cellY + y;

            if (xCoord > -1 && xCoord < size.width && yCoord > -1 && yCoord < size.height) {
              if (cells[yCoord][xCoord].mine) {
                count++;
              }
            }
          }
        }
      }

      return count;
    }

    const checkIfCompleted = () => {
      for (let y = 0; y < cells.length; y++) {
        for (let x = 0; x < cells[y].length; x++) {
          if (!cells[y][x].open && !cells[y][x].mine) {
            return false;
          }
        }
      }

      return true;
    }
  }
}

mineSweeper.run();