"use strict";

class CharacterCanvas {
  constructor(id, width, height, options = {}) {
    this._populateUnsetOptionsWithDefaults(options)
    this.options = options;

    this.rows = [];
    this.element = document.querySelector("#" + id);
    Object.assign(this, { width, height });

    this.monitorHeight = this.element.clientHeight;
    this.monitorWidth = this.element.clientWidth;
    this.rowHeight = this.monitorHeight / this.height;
    this.colWidth = this.monitorWidth / this.width;

    this.element.style.fontFamily = this.options.fontFamily;
    this.element.style.fontSize = this.rowHeight + "px";
    this.element.style.color = this.options.fontColor;
    this.element.style.backgroundColor = this.options.backgroundColor;

    this._createRows();
  }

  _createRows() {
    for (let i = 0 ; i < this.height ; i++) {
      const row = document.createElement("div");

      row.style.height = this.rowHeight + "px";

      row.classList.add("row");

      row.characters = [];

      for (let j = 0 ; j < this.width ; j++) {
        const character = document.createElement("span");
        const char = " ";
        character.innerHTML = char;
        character.style.width = this.colWidth + "px";
        character.style.height = this.rowHeight + "px";
        character.style.color = this.options.fontColor;
        character.style.backgroundColor = this.options.backgroundColor;

        character.getCoordinates = () => {
         return { x: j, y: i};
        }

        character.classList.add("character");

        if (this.options.hightlightCharacters) {
          character.classList.add("invert-on-hover");
        }

        row.characters.push(character);
        row.append(character);
      }

      this.rows.push(row);
      this.element.append(row);
    }
  }

  _populateUnsetOptionsWithDefaults(options) {
    const defaults = {
      fontFamily: "Courier New, Courier, monospace",
      fontColor: "white",
      backgroundColor: "black",
      hightlightCharacters: true
    }

    for (const def in defaults) {
      if (!options.hasOwnProperty(def)) {
        options[def] = defaults[def];
      }
    }
  }

  setCharacter(x, y, character, css = {}) {
    this.rows[y].characters[x].innerHTML = character;

    for (const style in css) {
      this.rows[y].characters[x].style[style] = css[style];
    }
  }

  clear() {
    for (let x = 0 ; x < this.height ; x++) {
      for (let y = 0 ; y < this.width ; y++) {
        this.rows[x].characters[y].innerHTML = " ";
      }
    }
  }
}

export default CharacterCanvas;